.ant-tabs {
  width: 300px;
  margin: auto !important;
}

.progress {
  margin-top: 30px;
  margin: 30px auto;
  width: 120px;
}

.custom-input {
  box-sizing: border-box;
  font-variant: tabular-nums;
  list-style: none;
  font-feature-settings: 'tnum', "tnum";
  position: relative;
  min-width: 0;
  color: rgba(0, 0, 0, 0.65);
  font-size: 14px;
  line-height: 1.5715;
  background-color: #fff;
  background-image: none;
  transition: all 0.3s;
  display: inline-block;
  width: 90px;
  height: 30px;
  margin: 0;
  padding: 0 11px;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
}

.start, .reset {
  width: 100px;
  height: 30px;
  border: none;
  background-color: #3af;
  color: #fff;
  outline: none;
  border-radius: 3px;
}

.start:hover {
  background-color: #3cf;
}

.reset {
  margin-left: 100px;
  background-color: #3a6;
}

.reset:hover {
  background-color: #4c6;
}

.reset:active {
  background: #063;
}

.timer {
  font-size: 50px;
  text-align: center;
}

.red {
  background-color: #f26;
}

.red:hover {
  background-color: #f56;
}